<template>
	<div class='broWrap'>
		<MyHearder>
			<span>广播</span>
			<img src="../../assets/images/ic_status_search_user.png" slot='leftImg'>
			<img src="../../assets/images/ic_chat_green.png" slot='rightImg'>
		</MyHearder>
		<div class="find">
			<div class="wenzi">
				<h3>发现有趣的人</h3>
				<p>关注他们，发现更大的世界</p>
			</div>
		</div>
		<transition-group name="fade">
			<BroadcastList v-for='item,index in guangbo' :name='item.name' :num='item.num' :xingqing='item.xingqing' :touxiang='item.touxiang' :key='item.id' @click='getClass'>
				<span class='guanbi' slot='guanbi' @click='guanbi(index)'>×</span>
				<img :src="item.peitu" slot='pt'>
			</BroadcastList>
		</transition-group>
		<div class="batch">
			<a class="left">换一批</a>
			<a class="right" :class='{green:num>0}'>选好了,进入广播</a>
		</div>
	</div>
</template>
<script type="text/javascript">
	import MyHearder from '@/components/hearder'
	import BroadcastList from '@/components/broadcastList'

	export default {
		components:{
			MyHearder,
			BroadcastList
		},
		data:function(){
			return {
				guangbo:[
					{	
						id:501,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'陆大鹏Hans',
						num:7658,
						xingqing:'今天上午签的',
						peitu:require('../../assets/images/ic_ad_baidu_market.png')
					},
					{	
						id:502,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'格里高尔',
						num:6967,
						xingqing:'这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字',
					},
					{
						id:503,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'君子之至',
						num:110,
						xingqing:'这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字',
					},
					{
						id:504,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'君子之至',
						num:110,
						xingqing:'这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字',
					},
					{
						id:505,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'君子之至',
						num:110,
						xingqing:'这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字',
					},
					{
						id:506,
						touxiang:require('../../assets/images/ic_share_default_user.png'),
						name:'君子之至',
						num:110,
						xingqing:'这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字这是一段文字',
					},
				],
				num:0
			}
		},
		methods:{
			getClass:function(bol){
				if(bol){
					this.num++
				}else{
					this.num--
				}
			},
			guanbi:function(index){
				this.guangbo.splice(index,1)
			}
		}
	}

</script>
<style type="text/css">
	.broWrap .batch{position: fixed;left: 0; bottom: 65px;width: 100%; height: 40px; background-color: #fff}
	.broWrap .batch a{ font-size: 12px;}
	.broWrap .batch .left{ float: left; padding-left: 40px; background-image: url(../../assets/images/ic_reshare_flag.png); background-size: 20px 20px;background-repeat: no-repeat; background-position: 15px 10px; color: #058805;line-height: 40px  }
	.broWrap .batch .right{ display: inline-block; float: right; margin-right: 20px; padding: 7px; background-color: #A1A1A1; height: 10px;line-height: 10px;margin-top: 5px;border-radius: 4px;color: #fff }
	.broWrap .find { width: 100%; background-image: url(../../assets/images/ic_status_anonymous_header.png); height: 100px; background-size: 80px 80px; background-repeat: no-repeat; background-position: 40px 10px;padding-bottom: 20px }
	.broWrap .find .wenzi { padding-left:130px; text-align: left;padding-top: 20px }
	.broWrap .find .wenzi p{ font-size: 12px; color: #999; padding-top: 10px }
	.broWrap .batch .green { background-color: #058805  }
</style>


